import React,{useState,useEffect} from 'react';
import './style.css';
import {NavLink} from 'react-router-dom';
import Card from '../UI/Card';

import blogPost from '../../data/blog.json';

const Sidebar=(props)=> {

    const[posts,setPosts]=useState([]);

    useEffect(()=>{
        const posts = blogPost.data;
        setPosts(posts);
    },[posts]);

    return (
        <div className="sidebarContainer" style={{
            width:'550px'
        }}>
             <Card style={{marginTop:'10px',marginBottom:'10px',padding:'10px',boxSizing:'border-box'}}>
                <div className="cardHeader">
                    <span>About Me </span>
                    <div className="profileImageContainer">
                        <img src="https://www.longfield-gardens.com/_ccLib/image/plants/DETA-483.jpg" alt=""/>
                    </div>
                    <div className="cardBody">
                        <p className="personalBio">My name is jessica leverett. I am a programmer specialization in front end development. :) 
                        I like to play with photoshop and other Adobe products also! 
                        Hollerrrrrrr!


                        </p>
                    </div>
                </div>
            </Card>

            <Card style={{marginBottom:'20px',padding:'10px',boxSizing:'border-box'}}>
                <div className="cardHeader">
                    <span>Social Network</span>
                </div>
            </Card>

            <Card style={{marginBottom:'20px',padding:'10px',boxSizing:'border-box'}}>
            <div className="cardHeader">
                    <span>Recent Post</span>
                </div>

                <div className="recentPosts">
                    {
                    posts.map(post=>{
                        return(
                            <NavLink key={post.id} to={`/post/${post.id}`}>
                            <div className="recentPost">
                                    <h3>{post.blogTitle}</h3>
                                    <span>{post.postedOn}</span>
                                </div>
                            </NavLink>
                        );
                     })
                    }
                </div>

            </Card>
        </div>
      
    )
}


export default Sidebar;