import React from 'react';
import './style.css';
import Card from '../../components/UI/Card';
import Sidebar from '../../components/Sidebar';
import RecentPosts from '../Home/RecentPosts'


const Home = props => {

    const galleryHeight=450;
    const galleryStyle={
        height:galleryHeight+'px',
        overflow:'hidden'
    }

    const sideImageHeight=galleryHeight/3;

    return (
        <div>
            <Card>
                <div className="galleryPost" style={galleryStyle}>
                <section style={{width:'70%'}}>
                    <div>
                    <img src={"https://cdn.pixabay.com/photo/2015/03/17/14/05/sparkler-677774_1280.jpg"} alt="pic"/>
                    </div>
                </section>
                <section className={"sideImageWrapper"} style={{width:'30%'}}>
                    <div style={{height:`${sideImageHeight}px`}}>
                    <img src={"https://cdn.pixabay.com/photo/2015/05/20/18/35/salad-775949_1280.jpg"} alt="flower"/>
                    </div>
                    <div style={{height:`${sideImageHeight}px`}}>
                    <img src={"https://cdn.pixabay.com/photo/2016/11/21/12/30/dark-1845065_1280.jpg"} alt="pic"/>
                    </div>
                    <div style={{height:`${sideImageHeight}px`}}>
                    <img src={"https://cdn.pixabay.com/photo/2013/11/28/10/36/road-220058_1280.jpg"} alt="pic"/>
                    </div>
                </section>
                </div>

            </Card>


        <section className="HomeContainer">
        

        <RecentPosts style={{width:'70%',color:'gray'}}/>
        <Sidebar/>


            </section>
      
        </div>
    );
       
    
}

export default Home;